<template>
  <div class="on_pn">
    <h2 class="tt_page"><span>Thm ti khon nhn vin</span></h2>  
    <div class="on_dskb">
      <div class="wrap">
        <div class="on_choose">
          <div class="on_it">
            <div class="it_3">
            <label>Chn ti khon:</label>
              <Dropdown v-model="customer" :options="listCustomer" optionLabel="name" placeholder="Chn ti khon" :filter="true" style="height: 35px" >

                <template #option="slotProps">
                  <div class="flex align-items-center" style="width: 40px; alignment: center">
                    <img v-if="slotProps.option.imageUrl" :src=slotProps.option.imageUrl style="width: 40px; margin-right: 3px" />
                    <label style="width: 40px; alignment: top">{{ slotProps.option.name }}</label>
                  </div>
                </template>
              </Dropdown>
          </div>
          <div class="it_3 it_ccc">
            <label>Chn khoa/phng</label>
          <Dropdown v-model="teamId" :options="teams" optionLabel="name" optionValue="code" placeholder="Chn khoa/ phng" :filter="true">
          </Dropdown>
          </div>
          <div class="it_3 it_bk">
          <Button label="ng k" icon="pi pi-plus-circle" iconPos="left" @click="doRegister()" />
          </div>

          </div>
        </div>
        
      </div>
      
    </div>
  </div> 

</template>

<script lang="ts">

import { computed, ref } from 'vue'
import AuthRepository from '@/services/AuthRepository'
import { useToast } from "primevue/usetoast";
import { useRouter } from 'vue-router'
import {Register} from "../models/register";
import {useStore} from "vuex";
import TeamRepository from "../services/TeamRepository";
import {Team} from "@/models/team.models";
import {ListCustomer} from "@/models/listCustomer";
import ZaloRepository from "@/services/ZaloRepository";
import {EmployeeCreate} from "@/models/employeeCreate";
import zaloRepository from "@/services/ZaloRepository";

export default {

  setup(){
    const router = useRouter();
    const register = ref({} as Register);
    const toast = useToast();
    const store = useStore();
    const error = ref(null);
    const customer = ref({} as ListCustomer);
    const teamId = ref("");
    const listCustomer = ref([] as ListCustomer[]);
    const teams = ref([] as Team[]);

    const employeeCreate = ref({} as EmployeeCreate);

    const valid = computed(()=> register.value.password && register.value.username && register.value.team && register.value.fullname && register.value.address);

    // if(!(store.state.permission == 'mster')){
    //   router.push({
    //     name: 'home'
    //   });
    // }
const getList = () => {
  ZaloRepository.getListCustomerWithOutEmployee()
      .then((response) => {
        listCustomer.value = response.data;
      })
      .catch(err => {
        toast.add({
          severity: 'error',
          summary: 'Li',
          detail:'Li khi ti danh sch khch hng',
          life: 2000
        });
      });
}
    getList();

    TeamRepository.getTeams()
        .then((response) => {
          teams.value = response.data;
        })
        .catch(err => {
          toast.add({
            severity: 'error',
            summary: 'Li',
            detail:'Li khi ti danh sch khoa phng',
            life: 2000
          });
        });

    const doRegister = () => {
      employeeCreate.value.zaloId = customer.value.id;
      employeeCreate.value.fullName = customer.value.name;
      employeeCreate.value.teamCode = teamId.value;
        zaloRepository.createEmployee(employeeCreate.value)
                .then((response) => {
                  toast.add({
                    severity: 'success',
                    summary: 'ng k',
                    detail: 'ng k thnh vin thnh cng vo h thng',
                    life: 2000
                  });
                  router.push({
                    name: 'showlistemployee'
                  });
                })
                .catch(err => {
                  toast.add({
                    severity: 'error',
                    summary: 'Li',
                    detail:err.response.data,
                    life: 2000
                  });
                });
    }

    return {
      register,
      doRegister,
      valid,
      teams,
      error,
      listCustomer,
      customer,
      teamId,
    }
  }
}

</script>
