<template>
        <Panel header="ng k ti khon nhn vin">
      <div class="p-fluid p-formgrid p-grid">
        <div class="p-field p-col p-col-12 p-md-6 p-lg-6">
          <label>Tn ng nhp</label>
          <InputText id="username" type="text" v-model="register.username" />
        </div>
        <div class="p-field p-col p-col-12 p-md-6 p-lg-6">
          <label>Mt khu(ti thiu 6 k t)</label>
          <Password id="password" v-model="register.password" toggleMask :feedback="true" />
        </div>
        <div class="p-field p-col p-col-12 p-md-6 p-lg-6">
          <label>H v tn</label>
          <InputText id="fullname" type="text" v-model="register.fullname" />
        </div>
<!--        <div class="p-field p-col p-col-12 p-md-6 p-lg-6">-->
<!--          <label>Chn n v</label>-->
<!--          <Dropdown id="donvi" v-model="register.companyCode" :options="donVi" optionLabel="ten" optionValue="ma" placeholder="Vui lng chn n v">-->
<!--          </Dropdown>-->
<!--&lt;!&ndash;          <InputText id="fullname" type="text" v-model="register.companyCode" />&ndash;&gt;-->
<!--        </div>-->
        <div class="p-field p-col p-col-12 p-md-6 p-lg-6">
          <label>Chn quyn hn</label>
          <Dropdown id="permission" v-model="register.permission" :options="accPermission" optionLabel="label" optionValue="value" placeholder="Vui lng chn quyn hn ca ti khon ...">
          </Dropdown>
        </div>
      </div>
      <Button v-if="valid" label="ng k" icon="pi pi-plus-circle" iconPos="left" @click="doRegister()" />
      <Button v-else label="ng k" icon="pi pi-plus-circle" iconPos="left" disabled="disabled" />
        </Panel>
</template>

<script lang="ts">

import { computed, ref } from 'vue'
import AuthRepository from '@/services/AuthRepository'
import { useToast } from "primevue/usetoast";
import { useRouter } from 'vue-router'
import {Register} from "../models/register";
import {useStore} from "vuex";
import TeamRepository from "../services/TeamRepository";
import {Team} from "@/models/team.models";
import {RegisterUser} from "@/models/registerUser";
import {AccountPermission} from "@/models/accountPermission";
import VaccinationRepository from "@/services/VaccinationRepository";
import {DonViCreate} from "@/models/donViCreate";

export default {

  setup(){
    const router = useRouter();
    const register = ref({} as RegisterUser);
    const toast = useToast();
    const store = useStore();
    const error = ref(null);

    const valid = computed(()=> register.value.password && register.value.username && register.value.fullname );

    console.log("@@@############### debug permission: " + store.state.permission);
    if(!(store.state.permission == 'admin')){
      router.push({
        name: 'home'
      });
    }
    const teams = ref({} as Team[]);

    const accPermission = ref([{
      label: "Qun l" , value: "admin"
    },
      {
        label: "Nhn vin" , value: "user"
      }]);


    TeamRepository.getTeams()
            .then((response) => {
              teams.value = response.data;
            })
            .catch();

    const doRegister = () => {
      if(register.value.password.length < 6)
      {
        toast.add({
          severity: 'error',
          summary: 'Tht bi',
          detail: 'Mt khu khng hp l, vui lng nhp li mt khu',
          life: 2000
        });
      }
      else {
        AuthRepository.registerUser(register.value)
                .then((response) => {
                  toast.add({
                    severity: 'success',
                    summary: 'ng k',
                    detail: 'ng k thnh vin thnh cng vo h thng',
                    life: 2000
                  });
                  router.push({
                    name: 'home'
                  });
                })
                .catch(err => {
                  toast.add({
                    severity: 'error',
                    summary: 'Li',
                    detail:err.response.data,
                    life: 2000
                  });
                });
      };
    }

    return {
      register,
      doRegister,
      valid,
      teams,
      accPermission,
      error,
      // donVi,
    }
  }
}

</script>
