          <div class="card flex justify-content-center">
            <Dropdown v-model="manager.idZalo" :options="listCustomer" optionLabel="name" optionValue="id" placeholder="Chn ti khon" :filter="true" class="w-full md:w-14rem">
              <template #option="slotProps">
                <div class="flex align-items-center">
                  <img v-if="slotProps.option.imageUrl" :src=slotProps.option.imageUrl style="width: 20px; margin-right: 3px" />
