          </Dropdown>
        </div>
        <div class="p-field p-col p-col-12 p-md-3 p-lg-3">
          <label>Thm qun l</label>
          <Button label="Thm" icon="pi pi-plus-circle" iconPos="left" @click="createManager()" :disabled="!manager.idZalo||!manager.idGroup"/>
        </div>
